.MainDiv {
  background-color: rgb(218, 216, 216);
  width: 100%;
  height: 200px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.mainNav {
  display: flex;
  height: 250px;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.mainHeadingDiv {
  display: flex;
  height: 200px;
  margin-top: 2rem;
  flex-direction: column;
  align-items: center;
}
.mainHeadingH1 {
  font-size: 4rem;
  text-align: center;
  font-weight: 300;
  font-family: "Roboto", sans-serif;
}
.mainHeadingH2 {
  text-align: center;
  font-size: 1rem;
  margin-top: 1rem;
  text-align: center;
  font-weight: 300;
}
.mainHeadingSearchDiv {
  margin-top: 1rem;
  display: flex;
  position: relative;
}
