.searchPicture {
  transition: 0.2s all;
}
.searchPicture:hover {
  transform: translateY(-2px);
}
.searchPicture:active {
  transform: translateY(2px);
}

.addAJobButton {
  background-color: red;
  position: absolute;
  padding: 1rem;
  color: white;
  border: none;
  top: 70px;
  right: 100px;
  cursor: pointer;
  font-size: 1.2rem;
  border-radius: 5px;
  text-decoration: none;
  outline: none;
  box-shadow: 1px 3px 4px rgb(0 0 0 / 10%);
}

/* header  mobile version */

@media only screen and (max-width: 600px) {
  .MainDiv {
    background-color: rgb(218, 216, 216);
    max-width: 100%;
    height: 700px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .mainHeadingH1 {
    font-size: 2rem;
    text-align: center;
    font-weight: 300;
  }
  .mainHeadingH2 {
    text-align: center;
    font-size: 1.2rem;
    margin-top: 1rem;
    text-align: center;
    font-weight: 200;
  }
}

@media only screen and (max-width: 900px) {
  .MainDiv {
    background-color: rgb(218, 216, 216);
    height: 300px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .mainHeadingH1 {
    font-size: 2rem;
    text-align: center;
    font-weight: 300;
  }
  .mainHeadingH2 {
    text-align: center;
    font-size: 1.2rem;
    margin-top: 1rem;
    text-align: center;
    font-weight: 200;
  }
}
